import React from 'react';
import styled from 'styled-components';

import { Table } from '../../Table';
import TableHead from '../../Table/TableHead';
import TableRow from '../../Table/TableRow';
import TableHeadItem from '../../Table/TableHeadItem';
import TableBody from '../../Table/TableBody';
import TableData from '../../Table/TableData';
import TableText from './TableText';

const LegalEntityTable = () => {
  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableHeadItem>
            Система налогообложения юридического лица
          </TableHeadItem>
          <TableHeadItem>
            Стоимость услуги* грн/месяц
          </TableHeadItem>
          <TableHeadItem>
            Стоимость доплаты за каждого следующего сотрудника
          </TableHeadItem>
        </TableRow>
      </TableHead>
      <TableBody>

        <TableRow>
          <TableData>
            <TableText>
              Упрощенная система налогооблажения, 5% (3 группа), без НДС
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              1500,00
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              150
            </TableText>
          </TableData>
        </TableRow>

        <TableRow>
          <TableData>
            <TableText>
              Упрощенная система налогооблажения, 3% (3 группа),+ НДС
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              5000,00
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              150
            </TableText>
          </TableData>
        </TableRow>

        <TableRow>
          <TableData>
            <TableText>
              Общая система налогооблажения без НДС
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              3000,00
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              150
            </TableText>
          </TableData>
        </TableRow>

        <TableRow>
          <TableData>
            <TableText>
              Общая система налогооблажения + НДС
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              5000,00
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              150
            </TableText>
          </TableData>
        </TableRow>

        <TableRow>
          <TableData>
            <TableText>
              Любая система налогооблажения
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              при отсутствии операций и начислений по зароботной плате
            </TableText>
          </TableData>
          <TableData>
            <TableText>
              500
            </TableText>
          </TableData>
        </TableRow>

      </TableBody>
    </Table>
  );
};

export default LegalEntityTable;
